<template>
    <v-container fluid class="pa-3">
        <v-layout wrap>
            <v-flex xs12 md8 offset-md2>
                <h1 class="heading">Posts</h1>
                <p> 
                    This application illustrates creation of a CRUD based Single Page Application (SPA) blog
                    using MEVN stack. <br>
                    I use <b> Vuejs and Vuetify</b> for the frontend and on the backend
                    <b> Nodejs and MongoDB</b>
                </p>
          <v-col  v-for="(post, index) in posts.slice((page - 1) *displayAmount , displayAmount * page)"  :key="post._id">   
          <v-card   color="#385F73" dark>
            <v-card-text class="white--text">
              <div class="headline mb-2">{{ post.title }} <span class="mySpan"> Author: {{ post.author }} </span> </div>
              {{ post.content }}
            </v-card-text>

            <v-card-actions>
              <v-btn color="success" @click="updatePostDialog(post)"> Update</v-btn>
              <div class="flex-grow-1"></div>
              <v-btn color="error" @click="$emit('removePost', index)"> Remove </v-btn>
            </v-card-actions>
          </v-card>
          </v-col>
            </v-flex>
            <v-flex xs12 md8 offset-md2>
                <v-pagination v-model="page" :length="Math.ceil(posts.length / displayAmount)"/> 
            </v-flex>
        </v-layout>

        <v-dialog v-model="updateDialog" persistent max-width="768">
        <v-card>
            <v-card-title class="grey darken-4 white--text headline">
                Update
            </v-card-title>
            <v-card-text>
                <v-container fluid grid-list-xl>
                    <v-layout wrap>
                        <v-flex xs12 md6>
                            <v-text-field label="Title" v-if="currentPost" v-model="currentPost.title"/>
                        </v-flex>
                        <v-flex xs12 md6>
                            <v-text-field label="Author" v-if="currentPost" v-model="currentPost.author"/>
                        </v-flex>
                        <v-flex xs12>
                            <v-textarea label="Content" v-if="currentPost" v-model="currentPost.content"/>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-card-actions class="grey darken-4">
                <v-btn @click="updateDialog = ! updateDialog" text color="grey"> Close </v-btn>
                <v-spacer></v-spacer>
                <v-btn @click="updatePost" text color="yellow"> Update Post </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
    </v-container>
</template>

<script>
export default {
    name: 'Dashboard',
    props: ['posts'],
    data (){
        return {
            updateDialog: false,
            currentPost: undefined,
            page: 1,
            displayAmount: 5
        }
    },
    methods: {
        updatePostDialog(post){
           this.currentPost = post;
           this.updateDialog = true; 
        },
        updatePost(){
            this.$emit('updatePost', this.currentPost);
            this.updateDialog = false;
        }
    }
}
</script>

<style scoped>
.mySpan {
  color: white;
  font-size: 14px;
  margin-right: 0;
  float: right;
}
</style>