<template>
  <div>
    <v-toolbar dark>
      <v-toolbar-title class="text-uppercase yellow--text">
          Vue-<span class="font-weight-light">Node-Mongo-</span> Blog
      </v-toolbar-title>

      <div class="flex-grow-1"></div>

      <v-toolbar-items>
        <v-btn @click="newPostDialog = !newPostDialog" text>New Post</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-dialog v-model="newPostDialog" persistent max-width="768">
        <v-card>
            <v-card-title class="grey darken-4 white--text headline">
                New Post
            </v-card-title>
            <v-card-text>
                <v-container fluid grid-list-xl>
                    <v-layout wrap>
                        <v-flex xs12 md6>
                            <v-text-field label="Title" v-model="newPost.title"/>
                        </v-flex>
                        <v-flex xs12 md6>
                            <v-text-field label="Author" v-model="newPost.author"/>
                        </v-flex>
                        <v-flex xs12>
                            <v-textarea label="Content" v-model="newPost.content"/>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-card-actions class="grey darken-4">
                <v-btn @click="newPostDialog = !newPostDialog" text color="grey"> Close </v-btn>
                <v-spacer></v-spacer>
                <v-btn @click="addPost" text color="yellow"> Add Post </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
    name: 'Navbar',
    data( ){
        return {
            drawer: false,
            newPostDialog: false,
            newPost: {}
        }
    },
    methods: {
        addPost(){
            let payload = {
                title: this.newPost.title,
                author: this.newPost.author,
                content: this.newPost.content,
            }

            fetch("http://localhost:5000/api/post/new" , {
                method: "POST",
                headers: {
                    "content-type": "application/json"
                },
                body: JSON.stringify(payload)
            })
            .then(data => {
                return data.json()
            })
            .then(json => {
                this.$emit('newPost', json.result);
                this.newPostDialog = false;
                this.newPost = {};
            });
        }
    }
}
</script>